import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../../components/Layout';
import Container from '../../components/Container';
import SpaceDivider from '../../components/SpaceDivider';
import Footer from '../../components/Footer';

import Block4Part from '../../components/Block4Part';
import BlockCTA from '../../components/BlockCTA';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockLogos from '../../components/BlockLogos';
import BlockQuote from '../../components/BlockQuote';

import TimeIcon from '../../components/icons/Time';
import MoneyIcon from '../../components/icons/Money';
import ShieldIcon from '../../components/icons/Shield';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    simpleTire: file(name: { eq: "simple-tire" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    OndemandTiresImage: contentfulCompany(name: { eq: "Ondemand tires" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    LEDMyplaceImage: contentfulCompany(name: { eq: "LEDMyplace" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    ikioImage: contentfulCompany(name: { eq: "ikio" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    supportGrowthImage: file(name: { eq: "support-growth" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    repeatCustomersImage: file(name: { eq: "repeat-customers" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    commonLivingSmall: file(name: { eq: "common-living-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    homeFurnishingsSmall: file(name: { eq: "home-furnishings-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fixed(width: 150) {
              ...GatsbyContentfulFixed
            }
          }
        }
      }
    }
  }
`;

export default function SEMLandingPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout landingNav>
      <GatsbySeo title="Automate your Accounts Receivables" />
      <Container className="lp">
        <BlockHeaderNoImage
          title="Automate your Accounts Receivables with Resolve"
          desc="Reduce the time you spend managing your Accounts Receivables, Resolve can automate this for you so you can focus on growing your business"
          primaryButtonText="Get A Demo"
          primaryButtonTo="/contact-sales"
        />

        <SpaceDivider height={50} />

        <BlockLogos
          pretitle="Trusted by the world's most innovative companies"
          data={[
            {
              image: companies['CozyEarth'],
              name: 'CozyEarth',
              offsetTop: 0,
            },
            {
              image: companies['Hyperikon'],
              name: 'Hyperikon',
            },
            {
              image: companies['Interior Define'],
              name: 'Interior Define',
              offsetTop: 0,
            },
            {
              image: companies['SimpleTire'],
              name: 'SimpleTire',
              offsetTop: 0,
            },
            {
              image: companies['Real Thread'],
              name: 'Real Thread',
              offsetTop: -6,
            },
          ]}
        />

        <SpaceDivider height={50} />

        <Block4Part
          title="Take the guesswork out of offering net payment terms"
          desc="Resolve takes care of all that for you"
          items={[
            {
              title: 'Speed Up Your Process',
              content: (
                <div>Spend less time chasing and processing invoices</div>
              ),
              icon: <TimeIcon />,
            },
            {
              title: 'Increase Working Capital',
              content: (
                <div>
                  Receive your payments sooner than the net payment terms
                  offered to your customers
                </div>
              ),
              icon: <MoneyIcon />,
            },
            {
              title: 'Reduce Your Risk',
              content: (
                <div>
                  Guaranteed payments to reduce the amount of receivables on
                  your books
                </div>
              ),
              icon: <ShieldIcon width="32" height="32" />,
            },
          ]}
        />

        <SpaceDivider height={100} />

        <BlockQuote
          title="Account Executive at Hyperikon"
          author="Ray Gil"
          image={companies['Hyperikon']}
        >
          "Resolve has been like a spring... allowing us to grow and scale your
          B2B business more quickly than any other company. It's been a catalyst
          to faster and better growth for us."
        </BlockQuote>

        <SpaceDivider height={100} />

        <BlockCTA
          pretitle="Ready to get started?"
          title="B2B companies love Resolve, but don't take our word for it"
          primaryButtonText="Get A Demo"
          primaryButtonTo="/contact-sales"
          align="center"
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .lp__section {
          text-align: center;
        }

        .lp__contact-us-button {
          display: block;
          margin: 0 auto;
        }

        .main-image {
          max-height: 300px;
        }

        .lp .app__actions button {
          margin-right: 0;
        }
      `}</style>
    </Layout>
  );
}
